html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding-inline: 0;
  padding-block: 3.125rem;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 400;
  font-family: var(--font-body);
  background-color: var(--main-bg);
  background-image: url("/images/bg-mobile-light.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% auto;
  transition: background-color 200ms ease-in;
}
@media (min-width: 40em) {
  body {
    padding-block: 5rem;
    background-image: url("/images/bg-desktop-light.jpg");
  }
}

body.dark-bg {
  background-image: url("/images/bg-mobile-dark.jpg");
}
@media (min-width: 40em) {
  body.dark-bg {
    background-image: url("/images/bg-desktop-dark.jpg");
  }
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --br-blue: hsl(220, 98%, 61%);
  --ck-bg-from: hsl(192, 100%, 67%);
  --ck-bg-to: hsl(280, 87%, 65%);
  --ul-bg: hsl(0, 0%, 98%);
  --main-bg: hsl(236, 33%, 92%);
  --linethrough-txt: hsl(233, 11%, 84%);
  --btn-txt: hsl(236, 9%, 61%);
  --ul-text: hsl(235, 19%, 35%);
  --btn-txt-hover: hsl(237, 14%, 26%);
}

body.dark-theme {
  --main-bg: hsl(235, 21%, 11%);
  --ul-bg: hsl(235, 24%, 19%);
  --ul-text: hsl(234, 39%, 85%);
  --btn-txt: hsl(234, 11%, 52%);
  --linethrough-txt: hsl(233, 14%, 35%);
  --btn-txt-hover: hsl(236, 33%, 92%);
}

body.light-theme {
  --ul-bg: hsl(0, 0%, 98%);
  --main-bg: hsl(236, 33%, 92%);
  --linethrough-txt: hsl(233, 11%, 84%);
  --btn-txt: hsl(236, 9%, 61%);
  --ul-text: hsl(235, 19%, 35%);
  --btn-txt-hover: hsl(237, 14%, 26%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --main-bg: hsl(235, 21%, 11%);
    --ul-bg: hsl(235, 24%, 19%);
    --ul-text: hsl(234, 39%, 85%);
    --btn-txt: hsl(234, 11%, 52%);
    --linethrough-txt: hsl(233, 14%, 35%);
    --btn-txt-hover: hsl(236, 33%, 92%);
  }
}
:root {
  --font-body: "Josefin Sans", sans-serif;
}

.container {
  max-width: 33.9375rem;
  width: 100%;
  margin-inline: auto;
  padding-inline: 1.5625rem;
}
@media (min-width: 40em) {
  .container {
    padding: 0;
  }
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2.5rem;
}
@media (min-width: 40em) {
  nav {
    margin-bottom: 3.125rem;
  }
}
nav .logo {
  font-size: 1.375rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.625rem;
  color: var(--white);
}
@media (min-width: 40em) {
  nav .logo {
    font-size: 2rem;
  }
}
nav button {
  width: 1.375rem;
  height: 1.375rem;
  border: none;
  padding: 0;
  font-size: 0.625rem;
  font-family: var(--font-body);
  background-color: transparent;
  background-image: url("/images/icon-moon.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  cursor: pointer;
}
@media (min-width: 40em) {
  nav button {
    width: 2rem;
    height: 2rem;
  }
}
nav button.night {
  background-image: url("/images/icon-sun.svg");
}

.input {
  display: flex;
  gap: 0.75rem;
  border-radius: 0.3125rem;
  margin-bottom: 1rem;
  padding-block: 0.9375rem;
  padding-inline: 1.25rem;
  background-color: var(--ul-bg);
  transition: background-color 200ms ease-in;
}
@media (min-width: 40em) {
  .input {
    gap: 1.25rem;
    margin-bottom: 1.5625rem;
    padding-block: 1.25rem;
    padding-inline: 1.5625rem;
  }
}
.input__checkbox {
  position: relative;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
}
@media (min-width: 40em) {
  .input__checkbox {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
.input__checkbox--button {
  width: 1.25rem;
  height: 1.25rem;
  margin: 0;
  appearance: none;
  border: 0.0625rem solid var(--linethrough-txt);
  border-radius: 50%;
  cursor: pointer;
  transition: border 200ms ease-in;
}
.input__checkbox--button:hover {
  border: 0.0625rem solid var(--btn-txt-hover);
}
@media (min-width: 40em) {
  .input__checkbox--button {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
.input__checkbox > input[type=checkbox]:checked + span {
  content: url("/images/icon-check.svg");
  position: absolute;
  top: 0%;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  padding: 0.3125rem;
  border: none;
  border-radius: 50%;
  background-image: linear-gradient(145deg, var(--ck-bg-from), var(--ck-bg-to));
  cursor: pointer;
}
@media (min-width: 40em) {
  .input__checkbox > input[type=checkbox]:checked + span {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
.input__text {
  width: 100%;
  outline: none;
  border: none;
  font-size: 0.75rem;
  font-family: var(--font-body);
  font-weight: 700;
  color: var(--ul-text);
  background-color: var(--ul-bg);
  transition: background-color 200ms ease-in;
}
@media (min-width: 40em) {
  .input__text {
    font-size: 1.125rem;
  }
}

ul {
  max-height: 23.125rem;
  overflow-y: auto;
  margin: 0;
  border-radius: 0.3125rem;
  padding: 0;
  list-style-type: none;
}
ul > :not(:last-child) {
  border-bottom: 0.0625rem solid var(--linethrough-txt);
}
@media (max-width: 39.9375em) {
  ul:not(:last-child) {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
}
@media (min-width: 40em) {
  ul {
    max-height: 27.5rem;
    margin-bottom: 1.5625rem;
  }
}
ul li {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  padding-block: 0.9375rem;
  padding-inline: 1.25rem;
  background-color: var(--ul-bg);
  transition: background-color 200ms ease-in;
  color: var(--ul-text);
}
@media (min-width: 40em) {
  ul li {
    gap: 1.25rem;
    padding-block: 1.25rem;
    padding-inline: 1.5625rem;
  }
}
ul li form label {
  position: relative;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
}
@media (min-width: 40em) {
  ul li form label {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
ul li form label input[type=checkbox] {
  width: 1.25rem;
  height: 1.25rem;
  margin: 0;
  appearance: none;
  border: 0.0625rem solid var(--linethrough-txt);
  border-radius: 50%;
  cursor: pointer;
  transition: border 200ms ease-in;
}
ul li form label input[type=checkbox]:hover {
  border: 0.0625rem solid var(--btn-txt-hover);
}
@media (min-width: 40em) {
  ul li form label input[type=checkbox] {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
ul li form label input[type=checkbox]:checked + span {
  content: url("/images/icon-check.svg");
  position: absolute;
  top: 0%;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  padding: 0.3125rem;
  background-image: linear-gradient(145deg, var(--ck-bg-from), var(--ck-bg-to));
  cursor: pointer;
}
@media (min-width: 40em) {
  ul li form label input[type=checkbox]:checked + span {
    width: 1.5625rem;
    height: 1.5625rem;
  }
}
ul li div {
  text-overflow: ellipsis;
  overflow-x: hidden;
  flex-grow: 1;
  font-size: 0.875rem;
  font-weight: 700;
  cursor: pointer;
}
@media (min-width: 40em) {
  ul li div {
    font-size: 1.125rem;
  }
}
ul li button {
  flex-shrink: 0;
  width: 0.8125rem;
  height: 0.8125rem;
  border: none;
  background-color: var(--ul-bg);
  background-image: url("/images/icon-cross.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  cursor: pointer;
  transition: background-color 200ms ease-in;
}
@media (min-width: 40em) {
  ul li button {
    width: 1.125rem;
    height: 1.125rem;
  }
}

.li-completed {
  color: var(--linethrough-txt);
  text-decoration: line-through;
}

.disable-edit {
  pointer-events: none;
}

.filter-m {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  border-bottom-left-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  padding-block: 0.9375rem;
  padding-inline: 1.25rem;
  background-color: var(--ul-bg);
  transition: background-color 200ms ease-in;
  border-top: 0.0625rem solid var(--linethrough-txt);
}
@media (min-width: 40em) {
  .filter-m {
    display: none;
  }
}
.filter-m span {
  font-size: 0.75rem;
  color: var(--linethrough-txt);
  transition: color 200ms ease-in;
}
.filter-m button {
  border: none;
  font-size: 0.75rem;
  font-weight: 700;
  font-family: var(--font-body);
  background-color: var(--ul-bg);
  color: var(--linethrough-txt);
  cursor: pointer;
  transition: all 200ms ease-in;
}
.filter-m button:hover {
  color: var(--btn-txt-hover);
}

.filter {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.6875rem;
  border-radius: 0.3125rem;
  padding-block: 0.9375rem;
  padding-inline: 1.25rem;
  background-color: var(--ul-bg);
  transition: background-color 200ms ease-in;
}
@media (min-width: 40em) {
  .filter {
    justify-content: space-between;
    padding-block: 1.25rem;
    padding-inline: 1.5625rem;
  }
}
.filter span {
  font-size: 0.75rem;
  color: var(--linethrough-txt);
  transition: color 200ms ease-in;
}
@media (max-width: 39.9375em) {
  .filter span {
    display: none;
  }
}
.filter__options > * {
  border: none;
  font-size: 0.9375rem;
  font-weight: 700;
  font-family: var(--font-body);
  background-color: var(--ul-bg);
  color: var(--btn-txt);
  cursor: pointer;
  transition: all 200ms ease-in;
}
.filter__options > *:hover {
  color: var(--btn-txt-hover);
}
.filter__options--active {
  color: var(--br-blue);
}
.filter__clear {
  border: none;
  font-size: 0.75rem;
  font-weight: 700;
  font-family: var(--font-body);
  background-color: var(--ul-bg);
  color: var(--linethrough-txt);
  cursor: pointer;
  transition: all 200ms ease-in;
}
.filter__clear:hover {
  color: var(--btn-txt-hover);
}
@media (max-width: 39.9375em) {
  .filter__clear {
    display: none;
  }
}

.note {
  text-align: center;
  font-size: 0.875rem;
  color: var(--btn-txt);
}

.modal {
  position: fixed;
  display: none;
  width: 20rem;
  border-radius: 0.3125rem;
  padding-inline: 1.25rem;
  padding-block: 1.875rem;
  background-color: var(--ul-bg);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media (min-width: 40em) {
  .modal {
    width: 28.125rem;
  }
}
.modal__show {
  display: block;
}
.modal header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.25rem;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
  color: var(--ul-text);
}
.modal header button {
  display: block;
  padding: 0;
  margin: 0;
  border: none;
  width: 0.8125rem;
  height: 0.8125rem;
  background-color: var(--ul-bg);
  background-image: url("/images/icon-cross.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  cursor: pointer;
}
@media (min-width: 40em) {
  .modal header button {
    width: 1.25rem;
    height: 1.25rem;
  }
}
.modal form textarea {
  margin-bottom: 0.625rem;
  width: 100%;
  border: none;
  background-color: var(--main-bg);
  color: var(--ul-text);
  resize: none;
}
.modal form label {
  display: block;
  margin-bottom: 0.3125rem;
  font-size: 0.75rem;
  color: lightgreen;
  visibility: hidden;
}
.modal form button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: var(--ul-bg);
  color: var(--ul-text);
  cursor: pointer;
}
@media (min-width: 40em) {
  .modal form button {
    font-size: 0.9375rem;
  }
}

.changes-saved {
  visibility: visible !important;
}

.modal-overlay {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  background-color: var(--main-bg);
  top: 0;
  opacity: 0.5;
  display: none;
}
.modal-overlay__show {
  display: block;
}